// src/App.scss

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
  padding-top: 0;
}

.page-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
  min-height: calc(100vh - 60px - 200px); // 헤더와 푸터 높이 제외
  
  h1 {
    color: #2d3748;
    margin-bottom: 16px;
    font-size: 2.5rem;
    font-weight: 700;
    text-align: center;
  }
  
  p {
    color: #4a5568;
    font-size: 1.1rem;
    line-height: 1.6;
    text-align: center;
  }
}

// 홈페이지 스타일
.hero-section {
  text-align: center;
  margin-bottom: 80px;
  padding: 30px 0;

  h1 {
    font-size: 3.5rem;
    color: #2d3748;
    margin-bottom: 20px;
    font-weight: 800;

    @media (max-width: 768px) {
      font-size: 2.5rem;
    }
  }

  p {
    font-size: 1.3rem;
    color: #718096;
    margin-bottom: 40px;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;

    @media (max-width: 768px) {
      font-size: 1.1rem;
    }
  }
}

.hero-actions {
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;

  @media (max-width: 768px) {
    flex-direction: column;
    gap: 15px;
  }
}

.cta-button {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 16px 32px;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);

  .button-icon {
    font-size: 1.2rem;
  }

  &--primary {
    background: linear-gradient(135deg, #4285f4 0%, #3367d6 100%);
    color: white;

    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 8px 25px rgba(66, 133, 244, 0.3);
    }
  }

  &--secondary {
    background: white;
    color: #38a169;
    border: 2px solid #38a169;

    &:hover {
      background: #38a169;
      color: white;
      transform: translateY(-2px);
      box-shadow: 0 8px 25px rgba(56, 161, 105, 0.3);
    }
  }

  @media (max-width: 768px) {
    width: 100%;
    max-width: 300px;
    justify-content: center;
  }
}

// 기능 소개 섹션
.features-section {
  h2 {
    font-size: 2.2rem;
    color: #2d3748;
    text-align: center;
    margin-bottom: 50px;
    font-weight: 700;
  }
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  margin-top: 40px;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

.feature-card {
  background: white;
  padding: 30px;
  border-radius: 16px;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;

  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
  }

  .feature-icon {
    font-size: 3rem;
    margin-bottom: 20px;
  }

  h3 {
    font-size: 1.4rem;
    color: #2d3748;
    margin-bottom: 15px;
    font-weight: 600;
  }

  p {
    color: #718096;
    font-size: 1rem;
    line-height: 1.6;
  }
}

// 반응형 디자인
@media (max-width: 768px) {
  .page-content {
    padding: 30px 15px;
    
    h1 {
      font-size: 2rem;
    }
    
    p {
      font-size: 1rem;
    }
  }
}

// 전역 스타일 리셋
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  background: #f8f9fa;
  color: #2d3748;
  line-height: 1.6;
}