.guide-content{max-width:800px;margin:0 auto;text-align:left}.guide-step{display:flex;gap:20px;margin-bottom:40px;padding:30px;background:#fff;border-radius:16px;box-shadow:0 5px 15px rgba(0,0,0,.08);transition:all .3s ease}.guide-step:hover{transform:translateY(-2px);box-shadow:0 10px 25px rgba(0,0,0,.12)}@media(max-width: 768px){.guide-step{flex-direction:column;gap:15px;padding:20px;margin-bottom:30px}}.guide-step .step-number{flex-shrink:0;width:50px;height:50px;background:linear-gradient(135deg, #4285f4 0%, #3367d6 100%);color:#fff;border-radius:50%;display:flex;align-items:center;justify-content:center;font-size:1.5rem;font-weight:bold;box-shadow:0 4px 15px rgba(66,133,244,.3)}@media(max-width: 768px){.guide-step .step-number{width:40px;height:40px;font-size:1.2rem;align-self:flex-start}}.guide-step .step-content{flex:1}.guide-step .step-content h3{font-size:1.5rem;color:#2d3748;margin-bottom:10px;font-weight:600}@media(max-width: 768px){.guide-step .step-content h3{font-size:1.3rem}}.guide-step .step-content p{color:#4a5568;font-size:1rem;margin-bottom:15px;text-align:left;line-height:1.6}.guide-step .step-content ul{list-style:none;padding:0;margin:0}.guide-step .step-content ul li{color:#718096;font-size:.95rem;margin-bottom:10px;padding-left:25px;position:relative;line-height:1.5}.guide-step .step-content ul li:before{content:"✓";color:#38a169;position:absolute;left:0;font-weight:bold;font-size:1rem}.guide-step .step-content ul li:last-child{margin-bottom:0}.guide-intro{background:linear-gradient(135deg, #667eea 0%, #764ba2 100%);color:#fff;padding:40px;border-radius:16px;text-align:center;margin-bottom:40px}.guide-intro h2{font-size:2rem;margin-bottom:15px;font-weight:700}.guide-intro p{font-size:1.1rem;opacity:.9;line-height:1.6}@media(max-width: 768px){.guide-content{padding:0 10px}}